 markdown: "<h2>Installation</h2>\n<pre><code class=\"language-shell\"># Using npm\nnpm install dashkit-ui\n\n# Using yarn\nyarn add dashkit-ui\n</code></pre>\n<h2>CDN</h2>\n<pre><code class=\"language-html\">&lt;link rel=&quot;stylesheet&quot; href=&quot;https://unpkg.com/dashkit-ui/dist/dashkit.css&quot; /&gt;\n&lt;script crossorigin src=&quot;https://unpkg.com/dashkit-ui/dist/dashkit.min.js&quot;&gt;&lt;/script&gt;\n</code></pre>\n<h2>Usage</h2>\n<h4>1. Import on demand</h4>\n<p>Use <a href=\"https://github.com/ant-design/babel-plugin-import\">babel-plugin-import</a> to import components on demand.</p>\n<pre><code class=\"language-js\"># Install plugin\nnpm i babel-plugin-import -D\n\n// set babel config in .babelrc or babel-loader\n// Note: Don't set libraryDirectory if you are using webpack 1.\n{\n  &quot;plugins&quot;: [\n    [&quot;import&quot;, {\n      &quot;libraryName&quot;: &quot;dashkit-ui&quot;,\n      &quot;libraryDirectory&quot;: &quot;es&quot;,\n      &quot;style&quot;: true\n    }]\n  ]\n}\n\n// For users who use babel7, that can be configured in babel.config.js\nmodule.exports = {\n  plugins: [\n    ['import', {\n      libraryName: 'dashkit-ui',\n      libraryDirectory: 'es',\n      style: function(stylePath) {\n        return `${stylePath}/style.scss`;\n      }\n    }, 'dashkit-ui']\n  ]\n};\n\n// Then you can import components from dashkit-ui\nimport { Button } from 'dashkit-ui'\n</code></pre>\n<p>If you are using TypeScript，please use <a href=\"https://github.com/Brooooooklyn/ts-import-plugin\">ts-import-plugin</a> instead</p>\n<h4>2. Manually import</h4>\n<pre><code class=\"language-javascript\">import Button from 'dashkit-ui/lib/button';\nimport 'dashkit-ui/lib/button/style.css';\n</code></pre>\n<h4>3. Import all components</h4>\n<pre><code class=\"language-javascript\">import React from 'react';\nimport Dashkit from 'dashkit-ui';\nimport 'dashkit-ui/lib/style/index.css';\n</code></pre>\n",
